import { dataFormat } from '../../frame_template/js/utils'
export default {
  data() {
    return {
      title: '', //标题
      pageSize: 10,
      currentPage: 1,
      tableData: [],
      pageList: []
    }
  },

  mounted() {
  	this.$nextTick(function() {
			this.queryDataCollectList();
		})
  },

  methods: {
  	dateFormat(row, column, cellValue, index) {
  		const daterc = row[column.property];
  		if (!!!daterc) {
		    return "";
		  }
  		return dataFormat(new Date(daterc), 'YYYY-MM-DD HH:mm:ss');
  	},
  	queryDataCollectList() {
  		this.$post('/admin/queryDataCollectList', { 'title': this.title, "page": this.currentPage - 1,
					"size": this.pageSize }).then((m) => {
  			this.tableData = m.content.page.content;
  			this.pageList = m.content.page;
			})
  	},
  	createData() {
  		sessionStorage.removeItem("DataCollectionID");
  		this.$router.push({ name: 'DataCollectionEdit'})
  	},
  	editData(id) {
  		sessionStorage.setItem("DataCollectionID", id);
  		this.$router.push({ name: 'DataCollectionEdit'})
  	},
  	deleteData(id) {
  		this.$confirm('删除后将无法恢复，确定删除该数据吗？', '提示', { confirmButtonText: '确定', cancelButtonText: '取消', type: 'warning' }).then(() => {
	  		this.$post('/admin/deleteDataCollect', { 'id': id }).then((m) => {
	  			if(m.resultCode == 0) {
		  				this.$message({
			    			showClose: true,
								type: 'success',
								message: '操作成功！'
							});
		  			}
	  			this.queryDataCollectList();
				})
  		}).catch(() => {
				this.$message({
	  			showClose: true,
					type: 'info',
					message: '已取消删除！'
				});
	    });
  	},
  	detailPage(item) {
  		let dataCollectItem = JSON.stringify(item);
  		sessionStorage.setItem("DataCollectionItem",dataCollectItem);
  		this.$router.push({ name: 'DataCollectionDetail'});
  	},
  	exportPage(id) {
			window.open('/ganzhoubaobiao/admin/exportSummaryData?id='+ id);
  	},
    handleSizeChange(val) {
    	this.currentPage = 1;
    	this.pageSize = val;
    	this.queryDataCollectList();
    },
    handleCurrentChange(val) {
    	this.currentPage = val;
    	this.queryDataCollectList();
    }
  }
}



